@font-face {
    font-family: 'Orbit-Regular';
    src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2310@1.0/Orbit-Regular.woff2') format('woff2');
    font-weight: normal;
    font-style: normal;
}

@import url('https://fonts.googleapis.com/css?family=Roboto:700');


.wrap_profile {
    width: 100%;
    height: 963px;
    position: relative;
    font-family: 'Exo 2', sans-serif;

      .domain {
        position: absolute;
        top: 5.5%;
        left: 42%;
        font-weight: bold;
        a {
            color: #fff;
        }

        a:hover {
           color: rgb(120, 133, 250);
        }
      }

    #profile {
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        color: #fff;

        .row {
            width: 100%;
            height: 100%;
            display: flex;

            .col-md-6.right_p  {
                display: flex;
                align-items: center;
                justify-content: flex-start;
            }

            .col-md-6 {
                width: 50%;
                height: 100%;
                display: flex;
                align-items: center;
                justify-content: flex-end;

                .profile_wrap {
                    width: 90%;
                    height: 90%;
                    display: flex;
                    flex-direction: column;
                    border: 1px solid #d4c9e9;
                    border-right: none;
                    background: rgb(58, 44, 96);
                    background: linear-gradient(0deg, rgba(58, 44, 96, 1) 0%, rgba(46, 42, 64, 1) 15%, rgba(31, 32, 52, 1) 31%, rgba(23, 24, 40, 1) 91%);

                   

                    .profile_top {
                        width: 100%;
                        height: 35px;
                        background-color: #d4c9e9;

                        p {
                            width: 17px;
                            height: 17px;
                            background-color: #fff;
                            float: left;
                            border-radius: 100%;
                            margin: 8px 5px 0 6px;

                        }

                        .p_1 {
                            margin-left: 10px;
                        }

                        .p_2 {
                            background-color: #6d4de2;
                        }

                        .p_3 {
                            background-color: #7c2ae6;
                        }
                    }

                    h1 {
                        margin: 45px 0 30px 55px;

                        .scoll_stripe {
                            transform: translateX(-300px);
                            color: #ccc;
                            line-height: 1;
                            position: relative;
                            display: block;
                            white-space: nowrap;
                            font-family: Arial;
                            font-weight: bold;
                            font-size: 48px;
                            text-transform: uppercase;
                            // transition: all 0.5s linear;
                        }

                        .stripe {
                            color: #f0f0f0;
                            line-height: 1;
                            position: relative;
                            display: block;
                            white-space: nowrap;
                            font-family: Arial;
                            font-weight: bold;
                            font-size: 48px;
                            text-transform: uppercase;
                            transform: translateX(0);
                            transition: all 0.4s linear;

                            cursor: pointer;

                            &:after {
                                content: 'PROFILE';
                                position: absolute;
                                left: 0;
                                top: 0;
                                line-height: 1;
                                display: block;
                                color: rgb(114, 102, 223);
                                width: 0;
                                overflow: hidden;
                                white-space: nowrap;
                                transition: 1s ease-out width;
                            }

                            &:hover:after {
                                width: 100%;
                            }
                        }
                    }

                    .produce_l {
                        width: 100%;
                        height: 350px;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        font-size: 18px;
                        font-family: 'Orbit-Regular';

                        .bling {
                            position: absolute;
                            top: 29%;
                            left: 42%;
                            height: 170px;
                        }

                        .sky_move {
                            transform: translateX(-300px);
                            width: 50%;
                            display: flex;
                            justify-content: center;
                            opacity: 0;
                            .me {
                                width: 70%;
                                height: 100%;
                                margin-left: 2px;
                                text-align: center;

                                & img {
                                    width: 100%;
                                    height: auto;
                                    border-radius: 100%;
                                }
                            }

                            .ring {
                                width: 400px;
                                position: absolute;
                                top: 29%;
                                left: 3%;
                            }
                            
                        }

                        .sky {
                            width: 50%;
                            display: flex;
                            justify-content: center;
                            transform: translateX(0);
                            position: relative;
                            transition: all 0.5s linear;
                            opacity: 1;

                            .me {
                                width: 70%;
                                height: 100%;
                                margin-left: 2px;
                                text-align: center;

                                & img {
                                    width: 90%;
                                    height: auto;
                                    border-radius: 100%;
                                }
                            }

                            .ring {
                                width: 400px;
                                position: absolute;
                                top: 29%;
                                left: 3%;
                            }
                        }

                        .p_text {
                            opacity: 1;
                            transition: all 1.5s linear;
                            line-height: 40px;
                            a {
                                color: #fff;
                            }

                            a:hover {
                                color: rgb(120, 133, 250);
                             }
                        }
                        
                        .p_move {
                            opacity: 0;
                        }

                    }

                    .edu {
                        opacity: 0;
                        width: 100%;
                        height: 50px;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        font-family: 'Orbit-Regular';
                    }

                    .edu_move {
                        width: 100%;
                        height: 50px;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        font-family: 'Orbit-Regular';
                        opacity: 1;
                        transition: 1.6s ease-in-out;
                    }

                    .interest {
                        width: 100%;
                        height: 250px;
                        display: flex;
                        flex-direction: column;
                        justify-content: space-evenly;

                        h3 {
                            margin: 20px 0 20px 55px;
                            font-size: 25px;
                        }

                        .icon {
                            width: 100%;
                            display: flex;
                            align-items: center;
                            justify-content: space-evenly;
                            // margin-top: 30px;

                            .icon_text {
                                text-align: center;

                                & img {
                                    width: 100px;
                                    margin-bottom: 15px;
                                }
                            }

                            .icon_text:hover {
                                opacity: 0.8;
                                cursor: pointer;
                                transform: scale(1.03);
                            }

                        }
                    }
                }

                .profile_r {
                    width: 90%;
                    height: 90%;
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    border: 1px solid #d4c9e9;
                    border-left: none;
                    background: rgb(58, 44, 96);
                    background: linear-gradient(0deg, rgba(58, 44, 96, 1) 0%, rgba(46, 42, 64, 1) 15%, rgba(31, 32, 52, 1) 31%, rgba(23, 24, 40, 1) 91%);

                    .profile_top {
                        width: 100%;
                        height: 35px;
                        background-color: #d4c9e9;  
                        p {
                            text-align: right;
                            margin: 4px 10px 0 0;
                            font-size: 20px;
                            font-weight: bold;
                        }                  
                    }

                    .planet {
                        position: absolute;
                        top: 17%;
                        right: 10%;
                        opacity: 0;
                        transform: translateY(-100px);
                    }

                    .planet_move {
                        position: absolute;
                        top: 17%;
                        right: 10%;
                        opacity: 1;
                        transform: translateY(0);
                        transition: all 0.4s ease-in;
                    }

                    #box_change {
                        color: #bfa8e9;
                        text-transform: uppercase;
                        font-size:50px;
                        font-weight:bold;
                        width:100%;
                        display:flex;
                        flex-direction: column;
                        align-items: center;
                        font-family:'Roboto';
                        text-align: center;
                        padding-top: 50px;
                        position: relative;
                      }
                      
                      #flip {
                        height:50px;
                        overflow:hidden;
                        .first_b {
                            padding: 2px 12px;
                            margin-top: -2px;
                        }
                      }
                      
                      #flip > div > div {
                        color:#fff;
                        padding: 4px 12px;
                        height:52px;
                        margin-bottom:35px;
                        display:inline-block;
                      }
                      
                      #flip div:first-child {
                        animation: show 5s linear infinite;
                      }
                      
                      #flip div div {
                        background:rgb(88, 228, 193);
                      
                      }
                      #flip div:first-child div {
                        background:#78c0fa;
                      }

                      #flip div:last-child div {
                        background:rgb(239, 241, 89);
                      }
                      
                      @keyframes show {
                        0% {margin-top:-270px;}
                        5% {margin-top:-180px;}
                        33% {margin-top:-180px;}
                        38% {margin-top:-90px;}
                        66% {margin-top:-90px;}
                        71% {margin-top:0px;}
                        99.99% {margin-top:0px;}
                        100% {margin-top:-270px;}
                      }
                      

                    h2 {
                        margin: 50px 0 30px 50px;
                        width: 88%;

                        .scoll_stripe {
                            transform: translateX(400px);
                            color: #ccc;
                            line-height: 1;
                            position: relative;
                            display: block;
                            white-space: nowrap;
                            font-family: Arial;
                            font-weight: bold;
                            font-size: 48px;
                            text-transform: uppercase;
                            // transition: all 0.5s linear;
                        }

                        .stripe {
                            color: #f0f0f0;
                            line-height: 1;
                            position: relative;
                            display: block;
                            white-space: nowrap;
                            font-family: Arial;
                            font-weight: bold;
                            font-size: 48px;
                            text-transform: uppercase;
                            transform: translateX(0);
                            transition: all 0.4s linear;

                            cursor: pointer;

                            &:after {
                                content: 'SKILLS';
                                position: absolute;
                                left: 0;
                                top: 0;
                                line-height: 1;
                                display: block;
                                color: rgb(128, 102, 223);
                                width: 0;
                                overflow: hidden;
                                white-space: nowrap;
                                transition: 1s ease-out width;
                            }

                            &:hover:after {
                                width: 100%;
                            }
                        }
                    }

                    .skill_logo_op {
                        opacity: 0;

                        width: 90%;
                        height: 300px;
                        display: flex;
                        flex-direction: column;
                        align-items: center;
                        justify-content: space-between;
                        font-size: 20px;
                        opacity: 0;

                        .s_logo {
                            width: 90px;
                            height: 140px;
                            float: left;
                            text-align: center;
                            flex-direction: column;
                            display: flex;
                            justify-content: space-between;
                            margin: 0 30px 0 30px;
                            & img {
                                width: 100%;
                                // margin-bottom: 10px;
                            }
                            .jq {
                                margin-top: 20px;
                            }
                            .sass {
                                margin-top: 30px;
                            }
                        }
                    }

                    .bling1 {
                        position: absolute;
                        top: 13%;
                        right: 21.5%;
                        & img {
                           height: 200px; 
                        }
                    }

                    .skill_logo {
                        width: 90%;
                        height: 300px;
                        display: flex;
                        flex-direction: column;
                        align-items: center;
                        justify-content: space-between;
                        font-size: 20px;
                        opacity: 1;
                        transition: all 2s ease;
                        position: relative;

                        .s_logo {
                            width: 90px;
                            height: 140px;
                            float: left;
                            text-align: center;
                            flex-direction: column;
                            display: flex;
                            justify-content: space-between;
                            margin: 0 30px 0 30px;
                            & img {
                                width: 100%;
                                // margin-bottom: 10px;
                            }
                            .jq {
                                margin-top: 20px;
                            }
                            .sass {
                                margin-top: 30px;
                            }
                        }
                    }

                    .heading {
                        // min-height: 100%;
                        display: flex;
                        flex-direction: column;
                        justify-content: center;
                        align-items: center;
                        font-family: "Raleway", sans-serif;
                        font-size: 1rem;
                        margin-top: 40px;
                      
                        &__title {
                          font-size: 7rem;
                          font-weight: 900;
                          text-align: center;
                          text-transform: uppercase;
                          line-height: 1;
                          letter-spacing: 0;
                          background-image: url("https://drive.google.com/uc?export=view&id=1N1sIi_qv615ZNV8ScTJ7NJcRsjHlpT4y");
                          background-repeat: repeat;
                          background-size: 400px auto;
                          -webkit-text-fill-color: transparent;
                          text-fill-color: transparent;
                          -webkit-background-clip: text;
                          background-clip: text;
                          -webkit-animation: stars 3s infinite linear;
                          animation: stars 3s infinite linear;
                      
                        }
                      
                      }
                      
                      @keyframes stars {
                        0% { background-position: 0 -400px; }
                        100% { background-position: 0 0; }
                      }
                      
                      
                      // Link
                      .link {
                        position: absolute;
                        bottom: 20px; left: 20px;
                        color: #271d54;
                        text-decoration: none;
                        
                        &:focus,
                        &:hover {
                          background-image: url("https://www.dropbox.com/s/ac9efh6oqu3docb/stars.png?raw=1");
                          background-repeat: repeat;
                          background-size: 200px auto;
                          -webkit-text-fill-color: transparent;
                          text-fill-color: transparent;
                          -webkit-background-clip: text;
                          background-clip: text;
                          -webkit-animation: stars 5s infinite linear;
                          animation: stars 5s infinite linear;
                        }
                      }
                }

            }
        }
    }
}
